import React, { useEffect } from 'react';

function Detail({ location, history }) {
    useEffect(() => {
        console.log(location);
        if (!location.state) {
            history.push('/');
        }
    });
    if (!location.state) {
        return null;
    }
    return (
    <div>
        <div>This is Detail Page</div>
        <div>{location.state.title}</div>
    </div>
    );
}

export default Detail;